import React, { useState, useEffect } from "react";
import "./Profile.css";
import { useAuth } from "../auth";
import axios from "axios";
import Developer from "./Developer";
const Profile = () => {
  const { user } = useAuth();
  const [tweetcount, settweetcount] = useState(0);
  const getTweets = async () => {
    const response = await axios.get("/api/user/number");
    settweetcount(response.data.data);
  };
  useEffect(() => {
    getTweets();
  });
  return (
    <>
      <div class="container  d-flex justify-content-center">
        <div class="row">
          <div>
            <div class="our-team">
              <div class="picture">
                <img class="img-fluid" src={user.profilePic} />
              </div>
              <div class="team-content">
                <h3 class="title">{user.name}</h3>
                <h4 class="name">{user.email}</h4>
                <h4 style={{ color: "white", padding: "2px" }} class="name">
                  {" "}
                  {tweetcount} tweets posted
                </h4>
              </div>
              <ul class="social"></ul>
            </div>
          </div>
          <div className="d-flex justify-content-center">
            <Developer />
          </div>
        </div>
      </div>
    </>
  );
};

export default Profile;
