import AddIcon from "@material-ui/icons/Add";
import { useState } from "react";
import { toast } from "react-toastify";
import axios from "axios";

function CreateNote(props) {
  const [entry, showEntry] = useState(false);
  const [title, settitle] = useState("");
  const [content, setcontent] = useState("");

  const handleSubmit = async () => {
    if (content.length === 0) return toast.error("Tweet field empty");
    try {
      const response = await axios.post("/api/user/addtweet", {
        title,
        content,
      });
      if (response.data.success) {
        toast.success("Tweet posted successfully!");
        props.getTweets();
        setcontent("");
        settitle("");
      }
    } catch (error) {
      toast.error(error.response.data.error);
    }
  };
  const show = () => {
    showEntry(true);
  };
  const hide = () => {
    showEntry(false);
  };

  return (
    <>
      <div className="main_note">
        <form className="formnote">
          {entry ? (
            <input
              onChange={(e) => settitle(e.target.value)}
              value={title}
              name="title"
              type="text"
              placeholder="Title"
              autoComplete="off"
              style={{ color: "black" }}
            />
          ) : null}
          <textarea
            rows=""
            column=""
            onChange={(e) => setcontent(e.target.value)}
            onClick={show}
            onDoubleClick={hide}
            value={content}
            name="content"
            placeholder="What's on your mind..."
            style={{ color: "white" }}
          ></textarea>
          {entry ? (
            <div class="btn-holder">
              {" "}
              <button
                type="button"
                style={{
                  float: "right",
                  backgroundColor: "#f5d020",
                }}
                onClick={() => handleSubmit()}
                className="btn"
              >
                <AddIcon />
              </button>
            </div>
          ) : null}
        </form>
      </div>
    </>
  );
}

export default CreateNote;
