import CreateNote from "./CreateNote";
import { useState, useEffect } from "react";
import axios from "axios";
import Card from "./Mycard";
import "./card.css";
import { toast } from "react-toastify";

function Notes() {
  const [addItem, SetAddItem] = useState([]);

  const getTweets = async () => {
    const response = await axios.get("/api/user/mytweets");
    SetAddItem(response.data.data);
  };
  const handleDelete = async (id) => {
    if (!window.confirm("Are you sure to delete this tweet?")) return;
    const response = await axios.delete(`/api/user/delete/${id}`);
    if (response.data.success) {
      toast.success("Tweet deleted");
      getTweets();
    }
  };
  useEffect(() => {
    getTweets();
  }, []);
  return (
    <>
      <CreateNote getTweets={getTweets} />
      <Card tweets={addItem} handleDelete={handleDelete} />
    </>
  );
}

export default Notes;
