import { useState, useEffect } from "react";
import axios from "axios";
import Card from "./FeedCard";
import "./card.css";
import { toast } from "react-toastify";

function Feed() {
  const [addItem, SetAddItem] = useState([]);

  const getTweets = async () => {
    const response = await axios.get("/api/user/alltweets");
    SetAddItem(response.data.data);
  };
  const handleFav = async (id, by) => {
    const response = await axios.get(`/api/user/fav/${id}/${by}`);
    if (response.data.success) {
      toast.success(response.data.message);
    }
  };
  useEffect(() => {
    getTweets();
  }, []);
  return (
    <>
      <Card tweets={addItem} handleFav={handleFav} />
    </>
  );
}

export default Feed;
