import { useState, useEffect } from "react";
import axios from "axios";
import Card from "./Favcard";
import "./card.css";
import { toast } from "react-toastify";

function Favourites() {
  const [addItem, SetAddItem] = useState([]);

  const getTweets = async () => {
    const response = await axios.get("/api/user/favtweets");
    SetAddItem(response.data.data);
  };
  const handleDelete = async (id) => {
    if (!window.confirm("Are you sure to remove this tweet?")) return;
    const response = await axios.delete(`/api/user/deletefav/${id}`);
    if (response.data.success) {
      toast.success("Tweet removed");
      getTweets();
    }
  };
  useEffect(() => {
    getTweets();
  }, []);
  return (
    <>
      <Card tweets={addItem} handleDelete={handleDelete} />
    </>
  );
}

export default Favourites;
