import React, { useState } from "react";
import Footer from "./Footer";
import Header from "./Header";
import logo from "../Images/logo.png";
import { useNavigate } from "react-router-dom";
import "./login.css";
import { toast } from "react-toastify";
import axios from "axios";
import Avatar from "@mui/material/Avatar";
import CircularProgress from "@mui/material/CircularProgress";

const Login = () => {
  const navigate = useNavigate();
  const [name, setname] = useState("");
  const [email, setemail] = useState("");
  const [password, setpassword] = useState("");
  const [cpassword, setcpassword] = useState("");
  const [ispicloading, setispicloading] = useState(false);
  const [profilePic, setProfilePicUrl] = useState(null);
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      if (profilePic === null) return toast.error("Upload profile picture");
      if (password.length < 5)
        return toast.error("Password should be atleast 6 characters");
      if (password !== cpassword)
        return toast.error("Incorrect confirm password");
      const response = await axios.post("/api/user/signup", {
        name,
        email,
        password,
        profilePic,
      });
      if (response.data.success) {
        toast.success("Successfully Registered!");
        navigate("/");
      }
    } catch (error) {
      toast.error(error.response.data.error);
    }
  };

  const handlePicUpload = (e) => {
    const image = e.target.files[0];
    console.log(image);
    if (
      image.type !== "image/jpg" &&
      image.type !== "image/png" &&
      image.type !== "image/jpeg"
    ) {
      toast.error("Upload jpg/png/jpeg File!", { position: "bottom-right" });
      return;
    }
    if (image.size > 2048 * 1024) {
      toast.error("File size exceeded 2MB!", { position: "bottom-right" });
      return;
    }
    const formdata = new FormData();
    formdata.append("file", image);
    formdata.append("upload_preset", "qvdaphyj");
    formdata.append("cloud_name", "enthuashu");
    setispicloading(true);
    axios
      .post("https://api.cloudinary.com/v1_1/enthuashu/image/upload", formdata)
      .then((response) => {
        setProfilePicUrl(response.data.secure_url);

        setispicloading(false);
        toast.success("Profile Image upload", { position: "bottom-right" });
      });
  };

  return (
    <>
      <Header />
      <div className="mt-2">
        <img alt="Profilepic" src={logo} height="70" />
      </div>
      <p class="texto mb-4"> Register</p>

      <div class="Registro">
        <form onSubmit={handleSubmit}>
          <div className="register_page_form_upload">
            {ispicloading ? (
              <div
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  padding: "5px",
                  margin: "10px",
                }}
              >
                <CircularProgress />
              </div>
            ) : (
              <>
                <p style={{ fontWeight: "bold" }} className="text-dark">
                  Profile Picture
                </p>

                {profilePic ? (
                  <>
                    <div
                      style={{
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "center",
                        padding: "5px",
                      }}
                      className="profile_pic_block"
                    >
                      <Avatar
                        alt="Profile Pic"
                        src={profilePic}
                        sx={{ width: 60, height: 60 }}
                        variant="rounded"
                      />
                    </div>
                    <p
                      className="text-dark mt-2"
                      onClick={() => setProfilePicUrl(null)}
                      style={{ cursor: "pointer" }}
                    >
                      Delete
                    </p>
                  </>
                ) : (
                  <>
                    <label htmlFor="clogo">
                      <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          justifyContent: "center",
                          padding: "5px",
                        }}
                        className="register_page_form_com_logo"
                        onClick={{}}
                      >
                        <Avatar
                          alt="Profile Pic"
                          sx={{ width: 60, height: 60 }}
                          variant="rounded"
                        />
                      </div>
                      <input
                        type="file"
                        accept="image/*"
                        name="profile_pic"
                        id="clogo"
                        onChange={(e) => handlePicUpload(e)}
                      />
                    </label>
                  </>
                )}
              </>
            )}
          </div>

          <span class="fontawesome-user"></span>

          <input
            type="text"
            required
            placeholder=" Enter Name"
            autocomplete="off"
            value={name}
            onChange={(e) => setname(e.target.value)}
          />
          <span class="fontawesome-envelope-alt"></span>
          <input
            type="text"
            id="email"
            required
            placeholder="Enter Email"
            autocomplete="off"
            value={email}
            onChange={(e) => setemail(e.target.value)}
          />
          <span class="fontawesome-lock"></span>
          <input
            type="password"
            name="password"
            id="password"
            required
            placeholder="Set Password"
            autocomplete="off"
            value={password}
            onChange={(e) => setpassword(e.target.value)}
          />
          <span class="fontawesome-lock"></span>
          <input
            style={{ marginBottom: "10px" }}
            type="password"
            name="cpassword"
            id="cpassword"
            required
            placeholder="Confirm Password"
            autocomplete="off"
            value={cpassword}
            onChange={(e) => setcpassword(e.target.value)}
          />
          <input type="submit" value="Register" title="Register" />
          <p
            onClick={() => navigate("/")}
            style={{ cursor: "pointer", color: "grey" }}
          >
            Already Registered? Login here
          </p>
        </form>
      </div>
      <Footer />
    </>
  );
};

export default Login;
