import React from "react";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import MyImage from "../Images/about.jpeg";
const Developer = () => {
  return (
    <>
      <div class="card-container mb-4 developer">
        <span class="pro">DEVELOPER</span>
        <img height={170} width={170} class="round " src={MyImage} alt="user" />
        <h3 style={{ margin: "10px 0", fontWeight: "bold" }}>
          Ashutosh Mishra
        </h3>
        <h6 style={{ margin: "5px 0" }}>Gorakhpur, UP, India</h6>
        <p style={{ fontSize: "14px", lineHeight: "21px" }}>
          Full Stack MERN Developer
        </p>
        <div class="buttons">
          <button
            onClick={() =>
              window.open("https://www.linkedin.com/in/enthuashu/")
            }
            class="primary mt-2"
          >
            <LinkedInIcon /> Linkedin
          </button>
        </div>
        <div class="skills">
          <h6>Skills</h6>
          <ul>
            <li>REACTJS</li>
            <li>NODEJS</li>
            <li>EXPRESSJS</li>
            <li>MONGODB</li>
            <li>REDUX</li>
            <br />
            <li>SOCKET</li>
            <li>AWS</li>
            <li>JAVASCRIPT</li>
            <li>C++</li>
            <li>HTML</li>
            <li>CSS</li>
          </ul>
        </div>
      </div>
    </>
  );
};

export default Developer;
