function Footer() {
  const year = new Date().getFullYear();
  return (
    <>
      <footer
        style={{
          backgroundColor: "#2c90c6",
          position: "fixed",
          bottom: 0,
          padding: "3px",
        }}
        className="tagline"
      >
        <p style={{ color: "white" }}>
          Copyright Â© {year} | Developed By{" "}
          <span
            style={{
              backgroundColor: "#d8b91c",
              padding: "5px",
              color: "black",
              cursor: "pointer",
            }}
            onClick={() =>
              window.open("https://www.linkedin.com/in/enthuashu/")
            }
          >
            Ashutosh Mishra{" "}
          </span>{" "}
        </p>
      </footer>
    </>
  );
}

export default Footer;
