import React from "react";
import "./card.css";
import DeleteOutlineIcon from "@mui/icons-material/DeleteOutline";
import Avatar from "@mui/material/Avatar";
import isoDiff from "../isoDiff";
const Card = ({ tweets, handleDelete }) => {
  return (
    <div>
      <div class="cards">
        {tweets.length > 0
          ? tweets.map((v, i) => {
              return (
                <>
                  <div key={i} class="card card-1">
                    <div
                      style={{
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "center",
                        padding: "3px",
                      }}
                    >
                      <Avatar src={v.id.profilePic} />
                    </div>

                    <h1 style={{ fontWeight: "bold" }} class="card__title">
                      {v.title}
                    </h1>
                    <div class="card__icon">
                      <i class="fas fa-bolt"></i>
                    </div>
                    <p class="card__exit">
                      <i class="fas fa-times"></i>
                    </p>
                    <h2 class="card__title content">{`"${v.content}"`}</h2>
                    <p class="card__apply">
                      <a style={{ fontStyle: "italic" }} class="card__link">
                        - By {v.id.name} <i class="fas fa-arrow-right"></i>
                      </a>
                    </p>
                    <div class="btn-holder">
                      {" "}
                      <p
                        style={{
                          float: "right",
                          fontSize: "12px",
                          padding: "5px",
                        }}
                      >
                        {isoDiff(v.createdAt)}
                      </p>
                      <button
                        type="button"
                        style={{
                          float: "left",
                          backgroundColor: "#2c90c6",
                          padding: "4px",
                        }}
                        title="Delete Tweet"
                        onClick={() => handleDelete(v._id)}
                        className="btn"
                      >
                        <DeleteOutlineIcon style={{ color: "black" }} />
                      </button>
                    </div>
                  </div>
                </>
              );
            })
          : null}
      </div>
    </div>
  );
};

export default Card;
